.exhibitions {
  margin-bottom: 120px;

  @include on-tablet {
    @include template {
      grid-template-columns: repeat(6, 1fr);
    }
  }

  @include on-desktop {
    @include template {
      grid-template-columns: repeat(12, 1fr);
    }
  }

  &__title {
    text-align: center;

    @include on-tablet {
      text-align: left;
      grid-column: 1 / span 4;
      grid-row: 1;
    }

    @include on-desktop {
      grid-column: 1 / span 6;
    }
  }

  &__card-one {
    @include on-tablet {
      grid-column: 1 / span 3;
      grid-row: 2;
    }

    @include on-desktop {
      grid-column: 1 / span 6;
    }
  }

  &__card-two {
    @include on-tablet {
      grid-column: 4 / span 3;
      grid-row: 2;
    }

    @include on-desktop {
      grid-column: 7 / span 6;
    }
  }

  &__btn {
    @include on-tablet {
      grid-column: 5 / span 2;
      grid-row: 1;
    }

    @include on-desktop {
      grid-column: 10 / span 3;
    }
  }
}
